.library__btn--flicker:before, .library__btn--flicker:after {
  content: "";
  width: 70px;
  height: 100%;
  background-color: var(--primary-text-header-color);
  filter: blur(20px);
  position: absolute;
  top: 0;
  left: -180px;
  transform: skewX(-30deg);
}

.library__btn--flicker:hover:before, .library__btn--flicker :focus:before {
  transition: all .3s;
  left: calc(100% + 150px);
}

.library__btn--flicker:hover:after, .library__btn--flicker :focus:after {
  transition: all .3s .3s;
  left: calc(100% + 150px);
}

