.header {
  padding-top: 40px;
  padding-bottom: 44px;
  background-color: #201f26;
  background-image: url(.././images/header/header-home-mobil-min.jpg);
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;

  @media (min-device-pixel-ratio: 2),
    (min-resolution: 192dpi),
    (min-resolution: 2dppx) {
    background-image: url(.././images/header/header-home-mobil-min-2x.jpg);
  }

  @media screen and (min-width: 768px) {
    background-image: url(.././images/header/header-home-tab-min.jpg);

    @media (min-device-pixel-ratio: 2),
      (min-resolution: 192dpi),
      (min-resolution: 2dppx) {
      background-image: url(.././images/header/header-home-tab-min-2x.jpg);
    }
  }

  @media screen and (min-width: 1280px) {
    background-image: url(.././images/header/header-home-desk-min.jpg);

    @media (min-device-pixel-ratio: 2),
      (min-resolution: 192dpi),
      (min-resolution: 2dppx) {
      background-image: url(.././images/header/header-home-desk-min-2x.jpg);
    }
  }
}

.site-nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 40px;

  &__list {
    display: flex;
  }

  &__item:not(:last-child) {
    margin-right: 20px;

    @media screen and (min-width: 768px) {
      margin-right: 40px;
    }
  }

  &__link {
    font-size: 12px;
    line-height: 1.16;
    color: var(--primary-text-header-color);
    text-transform: uppercase;
  }

  &__link::after {
    content: '';
    display: block;
    width: 100%;
    height: 3px;
    border-radius: 5px;
    background-color: var(--accent-header-color);
    opacity: 0;
    transition: opacity 250ms cubic-bezier(0.4, 0, 0.2, 1);
  }

  &__link:hover.site-nav__link::after {
    opacity: 1;
  }

  &__link-current.site-nav__link::after {
    opacity: 1;
  }
}

.logo {
  display: flex;
  align-items: center;

  &__text {
    font-size: 30px;
    line-height: 1.16;
    color: var(--primary-text-header-color);

    @media screen and (max-width: 767px) {
      display: none;
    }
  }

  &__icon {
    stroke: var(--primary-text-header-color);
    margin-right: 12px;
  }
}

.form-wrap {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.search-form {
  position: relative;
  margin-bottom: 20px;

  &__input {
    width: 280px;
    background-color: transparent;
    color: var(--primary-text-header-color);
    font-weight: 400;
    font-size: 14px;
    line-height: 1.14;
    padding: 4px;
    border: none;
    border-bottom: 1px solid var(--input-line-header-color);
    outline: none;
    transition: border-color 250ms cubic-bezier(0.4, 0, 0.2, 1);

    @media screen and (min-width: 768px) {
      width: 336px;
    }

    @media screen and (min-width: 1280px) {
      width: 394px;
    }

    &::placeholder {
      color: var(--primary-text-header-color);
    }
  }

  &__input:hover,
  &__input:focus {
    border-color: var(--accent-header-color);
  }

  &__btn {
    position: absolute;
    top: 0;
    right: 0;
    border: none;
    background-color: transparent;
    cursor: pointer;
  }
}

.warning-text {
  font-weight: 400;
  font-size: 14px;
  line-height: 1.14;
  color: var(--error-text-header-color);
}

.library {
  padding-top: 40px;
  padding-bottom: 60px;
  background-color: #201f26;
  background-image: url(.././images/header/header-library-mob-min.jpg);
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;

  @media (min-device-pixel-ratio: 2),
    (min-resolution: 192dpi),
    (min-resolution: 2dppx) {
    background-image: url(.././images/header/header-library-mob-2x-min.jpg);
  }

  @media screen and (min-width: 768px) {
    background-image: url(.././images/header/header-library-tab-min.jpg);

    @media (min-device-pixel-ratio: 2),
      (min-resolution: 192dpi),
      (min-resolution: 2dppx) {
      background-image: url(.././images/header/header-library-tab-2x-min.jpg);
    }
  }

  @media screen and (min-width: 1280px) {
    background-image: url(.././images/header/header-library-desk-min.jpg);

    @media (min-device-pixel-ratio: 2),
      (min-resolution: 192dpi),
      (min-resolution: 2dppx) {
      background-image: url(.././images/header/header-library-desk-2x-min.jpg);
    }
  }

  &__list {
    display: flex;
    justify-content: center;
  }

  &__item:not(:last-child) {
    margin-right: 20px;

    @media screen and (min-width: 768px) {
      margin-right: 32px;
    }

    @media screen and (min-width: 1280px) {
      margin-right: 16px;
    }
  }

  &__btn {
    min-width: 130px;
    height: 44px;
    border: 1px solid var(--primary-text-header-color);
    border-radius: 5px;
    background-color: transparent;
    cursor: pointer;
    font-size: 12px;
    line-height: 1.33;
    text-transform: uppercase;
    color: var(--primary-text-header-color);
    transition: background-color 250ms cubic-bezier(0.4, 0, 0.2, 1) 500ms,
      border-color 250ms cubic-bezier(0.4, 0, 0.2, 1) 500ms;

    &--flicker {
      position: relative;
      overflow: hidden;

      &::before,
      &::after {
        content: '';
        position: absolute;
        top: 0;
        left: -180px;
        width: 70px;
        height: 100%;
        background-color: var(--primary-text-header-color);
        filter: blur(20px);
        transform: skewX(-30deg);
      }

      &:hover::before,
      :focus::before {
        left: calc(100% + 150px);
        transition: 300ms;
      }

      &:hover::after,
      :focus::after {
        left: calc(100% + 150px);
        transition: 300ms 300ms;
      }
    }

    &:hover {
      background-color: var(--accent-header-color);
      border-color: var(--accent-header-color);
    }
  }

  @media screen and (min-width: 768px) {
    min-width: 152px;
  }

  @media screen and (min-width: 1280px) {
    min-width: 148px;
  }
}
